$main: #97ddf4;

.category {
    h1 {
        margin-top: 10rem;
        font-size: 4rem;
        color: rgba(0, 0, 0, 0.62);

        span {
            color: $main;
            font-size: 5rem;
        }
    }
}

@media (max-width:1400px) {

    .category {
        h1 {
            margin-top: 9rem;
            font-size: 3rem;

            span {
                font-size: 4rem;
            }
        }
    }
}

@media (max-width:1200px) {

    .category {
        h1 {
            margin-top: 8rem;
            font-size: 2.5rem;

            span {
                font-size: 3.5rem;
            }
        }
    }
}

@media (max-width:992px) {

    .category {
        h1 {
            margin-top: 7rem;
            font-size: 2rem;

            span {
                font-size: 3rem;
            }
        }
    }
}

@media (max-width:768px) {

    .category {
        h1 {
            margin-top: 10rem;
            font-size: 2.5rem;

            span {
                font-size: 3.5rem;
            }
        }
    }
}

@media (max-width:576px) {

    .category {
        h1 {
            margin-top: 8rem;
            font-size: 1.6rem;

            span {
                font-size: 2.5rem;
            }
        }
    }
}

@media (max-width:394px) {

    .category {
        h1 {
            margin-top: 7rem;
            font-size: 1.2rem;

            span {
                font-size: 1.5rem;
            }
        }
    }
}