$main: #97ddf4;

.cartPage {
    padding-top: 10rem;

    >h1 {
        font-family: 'Raleway', sans-serif;
        color: $main;

    }

    .row {
        margin-top: 5rem;

        .image_div {
            img {
                width: 50%;
                margin-left: 25%;
            }
        }

        .total {
            margin-top: 5rem;

            h3 {
                font-family: 'Raleway', sans-serif;
                color: $main;
            }

            h1 {
                color: $main;
                font-family: 'Raleway', sans-serif;

            }

            .btn {
                width: 20%;
                margin-left: 40%;
                background: none;
                color: $main;
                border: $main 1px solid;
                font-family: 'Raleway', sans-serif;
                font-size: 2rem;
                margin-top: 3rem;
            }

            .btn:hover {
                background: $main;
                color: white;

            }
        }

        .Remove_div {
            padding-top: 3rem;

            >div {
                padding: 2rem 25%;

                button {
                    border: none;
                    padding: 1rem 3rem;
                    outline: none;
                    border-radius: 50px;
                    font-size: 2rem;
                }
            }
        }

        .title_Div {
            padding-top: 3rem;
            font-family: 'Raleway', sans-serif;

            h1 {
                font-size: 2rem;
                color: black;

            }

            h3 {
                margin-top: 2rem;
                color: rgba(0, 0, 0, 0.403) !important;
                font-size: 1rem;
            }

            h2 {
                margin-top: 2rem;
                color: $main;
                font-size: 2.5rem;

                span {
                    color: rgba(0, 0, 0, 0.365);
                    font-size: 2rem;
                    text-decoration: line-through;
                }
            }

        }
    }
}



@media (max-width:1400px) {

    .cartPage {
        padding-top: 8rem;

        .row {
            .image_div {
                img {
                    width: 50%;
                }
            }

            .title_Div {
                padding-top: 2.3rem;

                h1 {
                    font-size: 1.5rem;
                }

                h3 {
                    margin-top: 2rem;
                    font-size: 1rem;
                }

                h2 {
                    margin-top: 2rem;
                    font-size: 2rem;

                    span {
                        font-size: 1.3rem;
                    }
                }

            }

            .Remove_div {
                padding-top: 5rem;

                >div {
                    padding: 1rem 25%;

                    button {
                        padding: 1rem 3rem;
                        font-size: 1.5rem;
                    }
                }
            }

            .total {
                margin-top: 5rem;

                .btn {
                    width: 20%;
                    margin-left: 40%;
                    font-size: 2rem;
                    margin-top: 2.5rem;
                }
            }

        }
    }
}



